<template>
  <el-container>
    <el-aside width="30%" >
        <div  class="logo">
          <img src="@/assets/img/keyfort.png"><br/>
        </div>        
        <div class="content">
          <img class="metamask" src="@/assets/img/metamask.png" alt=""><br/>
          <div class="tip">            
            {{$t("MetaMask is requesting to connect your device")}}
          </div>
        </div>        
    </el-aside>
    <el-main  v-loading="loading" :element-loading-text="loadingText">
      <div class="connect-tip" v-if = "status=='CONNECT-APP'">
        <div class="text">
          <ul>
            <li>{{$t("Allow getting your wallet address")}}</li>
            <li>{{$t("Allow to request transaction signature from the current wallet")}}</li>
          </ul>
        </div>
        <div class="img">
          <img src="@/assets/img/wallet.png" width="120" alt="">
        </div>
        <div class="form">
          <div style="text-align: left">
            <el-checkbox v-model="checked">{{$t("Always allow this service")}}</el-checkbox>
            <el-button type="primary" class="agree-btn" @click="connect()">{{$t("Agree")}}</el-button>
            <el-button  class="agree-btn" @click="reject()">{{$t("Cancel")}}</el-button>  
          </div>
        </div>
      </div>
      <div class="no-wallets-tip" v-else-if = "status=='NO-WALLETS'">        
        <div class="img">
          <img src="@/assets/img/empty.png" width="100" alt="">
        </div>
        <div class="text">
          {{$t("Current device wallet list is empty, please create wallet first")}}
        </div>
        <div class="form">
          <div>            
            <el-button type="primary" class="agree-btn" @click="noWalletClick()" v-if="walletListSource=='connect'">{{$t("I know")}}</el-button>
          </div>
        </div>
      </div> 
      <div class="wallet-list-tip" v-else-if = "status=='WALLET-LIST'">     
       
        <div class="text">
          <span v-if="walletListSource=='connect'">
            {{$t("Select wallet import")}}
          </span>
          <span v-else>
            {{$t("Select wallet trsaction")}}
          </span>          
        </div>
        <div class="form">
          <div class="radio-div">
              <div v-for="(item) in walletLists" :key="item.walletId">
                <el-radio v-model="selectWalletId" :label="item.walletId" >{{item.name}}</el-radio><br/>
              </div>         
          </div>
          <div>            
            <el-button type="primary" class="agree-btn" @click="selectWallet()">
              <span v-if="walletListSource=='connect'">
                {{$t("Improve")}}
              </span>
              <span v-else>
                {{$t("Ok")}}
              </span>
            </el-button>
            <el-button  class="agree-btn" @click="cancelImprove()"  v-if="walletListSource=='connect'">{{$t("Cancel")}}</el-button>  
          </div>
        </div>
      </div>         
      </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      walletLists: [],
      selectWalletId: undefined,
      checked: false,
      hdpath: undefined,
      ws: undefined,
      status: 'CONNECT-APP',
      extensionId : 'oobpjdfjeogiipeegcganhgbnpndnodi',
      loading: false,
      loadingText: this.$t('connect to KeyFort wallet'),
    };
  },
  created(){
    var getQueryVariable = function(variable)
    {
      var query = window.location.search.substring(1);
      var vars = query.split("&");
      for (var i=0;i<vars.length;i++) {
              var pair = vars[i].split("=");
              if(pair[0] == variable){return pair[1];}
      }
      return(false);
    }
    let ws = new WebSocket('ws://localhost:8001');
    this.ws = ws;
    let self = this;
    if(window.WebSocket){
      ws.onopen = function(e){
        //console.log("连接服务器成功",);
        self.loading = false;
        if(!getQueryVariable("signTransaction"))        
          //get the hdPath
          chrome.runtime.sendMessage(self.extensionId, {
            event: 'connect',
            data: undefined,
          },(hdPath)=>{
            //console.log("hdPath",hdPath);
            self.hdPath = hdPath;
          });
        else{
           self.loading = true;
          //console.log("链接交易");
          chrome.runtime.sendMessage(self.extensionId, {
            event: 'signTransactionLoaded',
            data: undefined,
          },(data)=>{
            
          });
        }       
      }
      ws.onclose = function(e){
        self.loading = false;
        self.ws = undefined;
        //console.log("服务器关闭");
       // self.status = "FAILED-CONNECT-APP";
        chrome.runtime.sendMessage(this.extensionId,{ 
          event: 'connectError'
        });
      }
      ws.onerror = function(){
        self.loading = false;
        self.ws = undefined;
        //console.log("连接出错");
        //self.status = "FAILED-CONNECT-APP";
        chrome.runtime.sendMessage(this.extensionId,{ 
          event: 'connectError'
        });
      }
      // 接收服务器的消息
      ws.onmessage = function(e){
        if(e.data != "end"){          
            var message = JSON.parse(e.data);
            //console.log(e.data,message);
            let result= message.data;
            if(result.errcode){
                self.loading = false;
                if(result.errcode == 5){
                  self.status = 'NO-WALLETS'
                }
                else{
                  self.alertDeviceError();
                  
                }
            }else{
                self.loading = false;
                if(message.event == 'walletLists'){
                    //console.log(message.data);
                    self.status = 'WALLET-LIST';
                    self.walletLists = result.res;
                    self.selectWalletId = result.res[0].walletId;
                    //ws.send(JSON.stringify({event:'getPublicKey',data:{hdPath:self.hdPath,walletId:message.data.res[0].walletId}}));
                }
                else if(message.event == 'publicKey'){
                    //console.log(message.data);
                   
                    chrome.runtime.sendMessage(self.extensionId,{ 
                      event: 'publicKey',
                      data: message.data,
                    },(data)=>{
                      //console.log("res data",data);
                      //ws.send("hdPath||"+hdPath)
                    });
                    
                }else if(message.event == 'signTransaction'){
                    //console.log(message.data);
                  // window.opener.postMessage(message.data,'*');

                    chrome.runtime.sendMessage(self.extensionId, {
                      event: 'signTransaction',
                      data: message.data,
                    },(data)=>{
                      //console.log("res data",data);
                    });
                    // chrome.runtime.sendMessage(this.extensionId, {
                    //   event: 'sinTransactionResult',
                    //   data: JSON.parse(message.data),
                    // },(data)=>{
                    
                    // });    
                }
            }  
        }else{
          self.loading = false;
          //console.log("end");
        }
      
      }   
    }else{
      //this.status = "FAILED-CONNECT-APP";
      chrome.runtime.sendMessage(this.extensionId,{ 
        event: 'connectError'
      });
    }
    window.addEventListener('message', function(event) {
      //可以通过event.origin  判断消息来源     
      let data = JSON.parse(event.data);      
      if (data.name == "signTransaction"){
         //console.log('做交易');
        let hdPath,tx;
        hdPath = data.hdPath;
        tx = data.tx;
        self.signTransaction({hdPath:hdPath,tx:tx});
      }
    });
  },
  mounted:function () {
   
  },
  methods:{
    reject(){
      chrome.runtime.sendMessage(this.extensionId,{ 
        event: 'connectError'
      });
    },
    alertDeviceError(){
      this.loading = false;
      this.$alert('<div><div class="img"><img src="'+require('@/assets/img/c300.png')+'" width="30" alt=""><br/><img src="'+require('@/assets/img/usb.png')+'" height="30" alt=""></div><div class="text">'+this.$t("Connect the device to the KeyFort App and make sure the device is connected")+'</div></div>', '', {
        dangerouslyUseHTMLString: true,
        customClass: 'device-error'
      });
    },
    alertAppConnectError(){
        this.$alert('<div class="img"><img src="'+require('@/assets/img/disconnect.png')+'" width="70" alt=""><div class="title">'+this.$t("Unconnected to KeyFort Wallet")+'</div><div class="tip">'+this.$t("Please follow the steps below")+'</div><div class="content"><ul><li>'+this.$t("appConnectError1")+'</li><li>'+this.$t("appConnectError2")+'</li><li>'+this.$t("appConnectError3")+'</li></ul></div></div>', '', {
          dangerouslyUseHTMLString: true,
          customClass: 'app-connect-error'
        });
    },
    getWalletLists(){
      //console.log(this.ws,this.walletListSource);
      if(this.ws){
        //this.ws.send(JSON.stringify({event:'getPublicKey',data:this.hdPath}));
        this.ws.send(JSON.stringify({event:'getWalletLists',data:this.hdPath}));
        this.loading = true;
      }else{
        this.alertAppConnectError();
      }    
    },
    connect(){
      this.walletListSource="connect"
      this.getWalletLists(); 
    },
    signTransaction(transactionData){
      this.walletListSource="signTransaction"
      this.signTransactionObj = transactionData;
      //console.log(this.walletListSource,"读钱包列表");
      this.getWalletLists();
    },
    noWalletClick(){
      this.status = 'CONNECT-APP'
    },
    selectWallet(){
      this.loading = true;
      //console.log("selectWallet",this.walletListSource);
      if(this.walletListSource=="connect"){
        this.ws.send(JSON.stringify({event:'getPublicKey',data:{hdPath:this.hdPath,walletId: this.selectWalletId}}));
      }else if(this.walletListSource=="signTransaction"){ 
        this.ws.send(JSON.stringify({event:'signTransaction',data:{hdPath:this.signTransactionObj.hdPath,tx:this.signTransactionObj.tx,walletId: this.selectWalletId}}));
      }               
    },
    cancelImprove(){
      this.status = "CONNECT-APP"
    }
  }
  // components: {
  //   HelloWorld
  // }
}
</script>
<style lang="less">
</style>
